<template>
    <div>
        <FloatBottle :bottle="bottleFloat" />
        <div class="container">
            <Bottle
                v-for="(bottle, index) in bottleArr"
                :key="index"
                :bottle_click="bottle_click"
                :bottle="bottle"
            />
        </div>
    </div>
</template>

<script>
import Bottle from "@/components/Bottle";
import FloatBottle from "@/components/FloatBottle";
import { nanoid } from "nanoid";
export default {
    name: "BottleGame",
    components: { Bottle, FloatBottle },

    data() {
        return {
            bottleArr: [],
            row: 2,
            col: 7,
            bottleFloat: {
                id: "",
                show: false,
                wines: [],
            },
        };
    },

    created() {
        let temp = require("@/assets/1.json");
        for (let i = 0; i < this.row * this.col; i++) {
            let bottle = {
                id: nanoid(),
                show: true,
                wines: temp[i],
            };
            this.bottleArr.push(bottle);
        }
    },

    methods: {
        bottle_click(id) {
            console.log(id);
            this.bottleArr.some((b) => {
                if (b.id == id) {
                    if (b.show) {
                        if (this.bottleFloat.show) {
                            // 倒入
                            if (
                                b.wines.length < 4 &&
                                this.bottleFloat.wines.length > 0
                            ) {
                                b.wines.unshift(this.bottleFloat.wines.shift());
                            }
                        } else {
                            // 拿起
                            this.bottleFloat = {
                                id: b.id,
                                show: true,
                                wines: b.wines.slice(),
                            };
                            b.show = false;
                        }
                    } else {
                        // 放下
                        this.bottleFloat.show = false;
                        b.show = true;
                        b.wines = this.bottleFloat.wines;
                    }
                    return true;
                }
            });
        },
    },
};
</script>

<style scoped>
.container {
    display: grid;
    grid-template-rows: repeat(2, 100px);
    grid-template-columns: repeat(7, 40px);
    gap: 20px;
    justify-content: center;
}
</style>